/* Navbar container styling */
.navbar {
  background-color: #153243; /* Dark blue background */
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative; /* Ensures proper stacking context */
  z-index: 10; /* Keep navbar on top */
}

/* Logo styling */
.navbar-logo {
  font-weight: bold;
  color: #e0f3fc; /* Light blue font color */
  font-size: 1.2em;
  text-transform: uppercase;
  text-decoration: none;
}

/* Right side container to hold navbar links and language switch */
.navbar-right {
  display: flex;
  align-items: center;
}

/* Navbar links styling */
.navbar-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
  margin: 0;
  align-items: center;
  z-index: 20; /* High z-index to ensure dropdown is on top */
}

.navbar-links li {
  display: inline;
}

.navbar-links a {
  color: #e0f3fc; /* Light blue for links */
  text-decoration: none;
  font-weight: normal;
  position: relative;
}

.navbar-links a:hover {
  text-decoration: underline;
}

/* Language switch styling */
.language-switch {
  display: flex;
  color: #e0f3fc;
  gap: 0.1rem;
  margin-left: 1rem;
  align-items: center;
}

.language-switch button {
  background-color: transparent;
  border: none;
  color: #e0f3fc;
  padding: 0.2rem;
  cursor: pointer;
  font-weight: normal;
}

.language-switch button:hover {
  text-decoration: underline;
}

.language-switch .active {
  font-weight: bold;
  text-decoration: none;
}

/* Burger menu icon styling */
.burger-menu {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #e0f3fc; /* Light blue for the burger icon */
}

/* Slide-in and Slide-out animations */
@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOut {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

/* Mobile styling */
@media (max-width: 768px) {
  .navbar-links {
    display: none;
    position: absolute;
    top: 62px;
    right: 0px;
    background-color: #59A5D8; /* Light blue background for dropdown */
    flex-direction: column;
    width: 160px; /* Slightly wider for better spacing */
    padding: 10px;
    margin-left: 10px; /* Added left margin */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    align-items: flex-start;
    z-index: 20; /* Ensure dropdown is above other elements */
  }

  .navbar-links.open {
    display: flex;
    animation: slideIn 0.3s forwards;
  }

  .navbar-links.closing {
    animation: slideOut 0.3s forwards;
  }

  .navbar-links li {
    margin-bottom: 10px;
  }

  /* Align language switch to the left in mobile dropdown */
  .navbar-links .language-switch {
    margin-top: 10px;
    margin-left: 0;
    justify-content: flex-start;
    width: 100%;
  }

  /* Show the burger menu icon on mobile */
  .burger-menu {
    display: block;
  }
}
