/* Main news page container */
.news-page {
    margin: 40px 20px;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
  }
  
  /* Page title */
  .news-page h2 {
    font-size: 2em;
    font-weight: bold;
    color: #153243;
    margin-bottom: 20px;
    text-align: center;
  }
  
  /* News list container */
  .news-list {
    display: flex;
    flex-direction: column;
    gap: 0; /* No gap as we use bottom borders */
  }
  
  /* Full-width news item styling */
  .news-list-item {
    display: flex;
    padding: 30px 0; /* Increased padding for a more spacious look */
    border-bottom: 1px solid #ddd; /* Bottom border only */
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
  }

  .news-list-item:hover {
    transform: scale(1.02);
  }
  
  
  /* News image */
  .news-image {
    width: 180px; /* Slightly larger for emphasis */
    height: 180px;
    margin-right: 30px; /* Increased space between image and text */
    object-fit: cover;
  }
  
  /* News details */
  .news-details {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .news-title {
    font-size: 1.6em;
    color: #153243;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .news-date {
    font-size: 0.9em;
    color: #666;
    margin-bottom: 15px;
  }
  
  .news-description {
    font-size: 1em;
    color: #444;
    line-height: 1.6;
  }
  
/* Pagination styling */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    gap: 15px;
  }
  
  .pagination-info {
    font-size: 1em;
    color: #666;
  }
  
  /* Pagination links */
  .pagination a {
    color: #0056b3; /* Link color */
    cursor: pointer;
    text-decoration: none;
    font-weight: normal;
    transition: color 0.3s;
  }
  
  .pagination a:hover {
    color: #003366; /* Darker color on hover */
  }
  
  .pagination a:disabled {
    color: #aaa;
    pointer-events: none;
    text-decoration: none;
  }
  