/* Event card styling */
  .event-card {
    width: 280px;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
    transition: transform 0.3s ease;
  }
  
  
  /* Event icon styling */
  .event-icon-container {
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 30px 0px;
  }
  
  .event-icon-circle {
    width: 60px;
    height: 60px;
    background-color: #59A5D8; /* Blue background for the icon circle */
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .event-icon {
    width: 30px; /* Icon size */
    height: 30px;
    object-fit: contain;
    color: #fff;
  }
  
  /* Event info styling */
  .event-info h3 {
    font-size: 1.1em;
    color: #153243;
    margin: 20px 0px;
  }
  
  .event-underline {
    width: 40px;
    height: 3px;
    background-color: #59A5D8; /* Medium blue color */
    margin: 0 0 10px 0;
  }
  
  