.App {
  text-align: center;
}

/* Ensure the app container fills the viewport height */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Content container takes up remaining space */
.content-container {
  flex: 1;
  padding: 2vh 4vw;
  margin: 5vh 16vw;
}

h1 {
  font-size: 2.5em;
  font-weight: 700;
  color: #333; /* Darker color for heading */
  margin-bottom: 20px;
}

.center {
  text-align: center;
}

@media (max-width: 768px) {
  .content-container {
    padding: 2vh 2vw;
    margin: 5vh 2vw;
  }
}
