/* Member detail page container */
.member-detail {
  margin: 40px 20px;
  max-width: 800px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

/* Profile image styling */
.member-detail-image {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.member-image-container {
  text-align: center;
}

/* Member name and occupation styling */
.member-name {
  font-size: 2em;
  font-weight: 600;
  color: #153243;
  margin: 10px 0 5px;
  text-align: center;
}

.member-occupation {
  font-size: 1.1em;
  color: #888;
  margin-bottom: 20px;
  text-align: center;
}

/* Bio and information text */
.member-bio {
  text-align: justify;
  color: #444;
  font-size: 1em;
  line-height: 1.6;
  margin-top: 20px;
}

.member-content {
  margin-top: 60px;
}

/* Label styling */
.member-detail p strong {
  color: #666;
  font-weight: 500;
}

/* Publications section styling */
.publications-title {
  font-size: 1.4em;
  color: #153243;
  margin-top: 30px;
  margin-bottom: 15px;
  text-align: left;
}

/* Publications list styling */
.publications-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  text-align: left;
}

.publication-item {
  margin-bottom: 15px;
  font-size: 0.95em;
  line-height: 1.4;
  color: #444;
}

.publication-link {
  color: #59A5D8;
  text-decoration: none;
  font-weight: 500;
}

.publication-link:hover {
  text-decoration: underline;
}

.back-button {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  color: #59A5D8; /* Link color */
  cursor: pointer;
  margin-bottom: 60px;
  font-weight: 550;
  font-size: 1rem;
}

.back-button:hover {
  color: #59A5D8;
  text-decoration: underline;
}

.back-icon {
  width: 16px;
  height: 16px;
  margin-right: 8px;
}

.publication-link:hover, .back-button:hover {
  color: #59A5D8;
}
