/* Base button styling */
.button {
  font-size: 1em;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
}

/* Solid button style */
.button.solid {
  background-color: #59A5D8; /* Light blue */
  color: #fff;
  border: none;
}

.button.solid:hover:not(.button:disabled) {
  background-color: #468abf; /* Darker blue on hover */
  transform: scale(1.02); /* Slight scaling effect */
}

/* Outline button style */
.button.border {
  background-color: transparent;
  color: #59A5D8; /* Light blue for text */
  border: 2px solid #59A5D8;
}

.button.border:hover:not(.button:disabled) {
  background-color: #e0f3fc; /* Light background on hover */
  color: #468abf; /* Darker blue text on hover */
  border-color: #468abf; /* Darker border on hover */
  transform: scale(1.05); /* Slight scaling effect */
}

/* Disabled button styling */
.button:disabled {
  background-color: #ccc; /* Light gray for disabled state */
  color: #666; /* Dark gray text for disabled */
  border-color: #ccc; /* Matching border for outline variant */
  cursor: default;
  transform: none; /* Remove scaling */
}
