/* Members page container */
.members-page {
  margin: 40px 20px;
  text-align: center;
}

.members-page h2 {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.members-subtitle {
  font-size: 1em;
  color: #666;
  margin-bottom: 20px;
}

/* Members grid styling */
.members-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

/* Member card styling */
.member-card {
  background-color: #fff;
  padding: 80px 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.member-card:hover {
  transform: translateY(-5px);
}

/* Member image */
.member-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-bottom: 10px;
  object-fit: cover;
}

/* Member info styling */
.member-info {
  margin-top: 10px;
}

.member-occupation {
  font-size: 0.9em;
  color: #888;
  margin-bottom: 10px;
}

.member-name {
  font-size: 1.1em;
  color: #333;
  margin-bottom: 10px;
  font-weight: 600;
}

/* Research interest at the bottom of the card */
.member-research-interest {
  font-size: 0.85em;
  color: #888;
  margin-top: 10px;
  padding: 0 10%;
}

/* Responsive styling for small screens */
@media (max-width: 600px) {
  .members-grid {
    grid-template-columns: 1fr; /* Single column on small screens */
  }
  
  .member-card {
    padding: 60px 15px; /* Adjust padding for smaller screens */
  }

  .member-image {
    width: 70px; /* Slightly smaller image size on small screens */
    height: 70px;
  }

  .member-name {
    font-size: 1em; /* Adjust font size for smaller screens */
  }

  .member-occupation, .member-research-interest {
    font-size: 0.8em; /* Adjust font size for smaller text */
  }
}
