.footer {
    position: relative;
    z-index: 2;
    background-color: #846C5B;  /*  #561D25 */
    color: white;
    padding: 20px 0;
    text-align: center;
    margin-top: auto;
  }
  
  .footer a {
    color: #153243;  /* Set a desired color for the links */
    text-decoration: none;  /* Remove underline */
    margin: 0px;
  }
  
  .footer a:hover {
    text-decoration: underline;  /* Add underline on hover for clarity */
    color: #153243;  /* Optional: Change color on hover */
  }
  
  .footer-columns {
    text-align: left;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap; /* Allows columns to wrap on smaller screens */
  }
  
  .footer-column {
    flex: 1;
    max-width: 300px;
    margin: 10px;
  }
  
  @media (max-width: 768px) {
    .footer-columns {
      flex-direction: column;
    }
  
    .footer-column {
      margin: 10px 0;
    }
  }
  