/* Main events page styling */
.events-page {
  margin: 40px 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.events-page h2 {
  font-size: 2em;
  font-weight: bold;
  color: #153243;
  margin-bottom: 20px;
  text-align: center;
}

/* Events list container */
.events-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* Individual event item card styling (matches homepage) */
.event-item {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  width: calc(50% - 20px); /* Two columns on larger screens */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: box-shadow 0.3s ease;
}

.event-item:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Event image styling */
.event-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

/* Event details styling */
.event-details h3 {
  font-size: 1.2em;
  color: #153243;
  margin-bottom: 5px;
}

.event-date {
  font-size: 0.9em;
  color: #666;
  margin-bottom: 10px;
}

.event-description {
  font-size: 1em;
  color: #444;
  line-height: 1.6;
}

/* Pagination styling */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

.pagination-info {
  font-size: 1em;
  color: #666;
}

/* Responsive styling for smaller screens */
@media (max-width: 768px) {
  .event-item {
    width: 100%; /* Stack events on small screens */
  }
}
