/* Home container styling */
.home {
    padding: 20px;
  }

    /* Introductory section styling */
    .home-section {
      padding: 80px 0px;
    }
    
  
  /* Introductory section styling */
  .home-intro {
    max-width: 700px;
    margin: 0 auto auto 0;
    text-align: left;
  }
  
  .home-intro h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #333; /* Darker color for heading */
    margin-bottom: 20px;
  }
  
  .home-intro p {
    font-size: 1.1em;
    color: #666; /* Lighter gray for paragraphs */
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  /* Button container */
  .home-buttons {
    display: flex;
    gap: 15px;
    margin-top: 20px;
  }
  