/* Latest News Section styling */
.latest-news-section {
  margin: 40px 0;
  text-align: left;
}

.latest-news-container {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin-top: 20px;
}

.news-item {
  width: 30%;
  cursor: pointer;
  padding: 10px;
  text-align: left;
  background-color: #fff;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.news-item:hover {
  transform: scale(1.02); /* Slightly enlarge on hover */
}

.news-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 10px 10px 10px 10px; /* Rounded corners at the top */
}

.news-info {
  padding: 15px 10px;
}

.news-date {
  font-size: 0.85em;
  color: #666;
  margin-bottom: 5px;
}

.news-info h3 {
  margin: 5px 0;
  font-size: 1.1em;
  color: #153243; /* Medium blue color */
}

.news-info p {
  color: #666;
  font-size: 0.9em;
  margin-top: 5px;
}

/* View All News Link styling */
.view-all-news-container {
  text-align: center;
  margin-top: 20px;
}

.view-all-news-link {
  font-size: 1em;
  color: #153243;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
}

.view-all-news-link:hover {
  text-decoration: underline;
}
